@charset "UTF-8";
.contact_form {
  display: flex;
  align-items: center; }
  .contact_form_section {
    background: #103989;
    padding: 75px 0; }
  .contact_form_title {
    max-width: 800px; }
  .contact_form .input {
    min-height: 117px; }
  .contact_form .input-black .input-field {
    background: #fff;
    border: 1px solid #979797;
    border-radius: 4px; }
  .contact_form .input-field {
    color: #292527 !important; }
    .contact_form .input-field:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 50px #fff !important;
      /* Цвет фона */
      color: #292527 !important;
      /* цвет текста */
      -webkit-text-fill-color: #292527 !important; }
    .contact_form .input-field:-webkit-autofill:focus {
      -webkit-box-shadow: inset 0 0 0 50px #fff !important;
      /* Цвет фона */
      color: #292527 !important;
      /* цвет текста */
      -webkit-text-fill-color: #292527 !important; }
  .contact_form_profile {
    width: calc(25% - 30px);
    margin-right: 30px; }
    .contact_form_profile .form_btn {
      bottom: 17px;
      position: relative;
      width: 100%;
      padding: 14.5px 25px; }

.label_input {
  display: block;
  margin-bottom: 10px; }

@media (max-width: 1040px) {
  .contact_form {
    flex-direction: column; }
    .contact_form_profile {
      width: 100%;
      margin-right: 0;
      margin-top: 20px; }
    .contact_form .input {
      min-height: unset;
      margin: 0px 0px; }
    .contact_form_section {
      background: #103989;
      padding: 20px 0px; } }
