.footer {
  position: relative;
  background: #292527;
  padding: 30px 0; }
  .footer__block {
    display: flex;
    justify-content: space-between; }
  .footer_logo {
    width: 100%;
    margin-bottom: 10px; }
    .footer_logo img {
      width: 100%; }
    .footer_logo__block {
      width: 180px; }
  .footer_service__block {
    width: calc(100% - 450px);
    margin: 0px 30px; }
  .footer_service_link {
    transition: all .2s;
    width: 45%;
    margin-top: 10px;
    margin-right: 30px; }
    .footer_service_link__block {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start; }
    .footer_service_link:hover {
      color: #ea1a28; }
  .footer_nav__block {
    width: 270px; }
  .footer_nav_title {
    margin-bottom: 10px; }
  .footer_nav_link {
    transition: all .2s;
    margin-bottom: 10px; }
    .footer_nav_link__block {
      display: flex;
      flex-direction: column; }
    .footer_nav_link:hover {
      color: #ea1a28; }

@media (max-width: 1228px) {
  .footer_nav__block {
    width: 70px; }
  .footer_service__block {
    width: calc(100% - 250px); } }

@media (max-width: 992px) {
  .footer_service_link {
    width: 100%; } }

@media (max-width: 530px) {
  .footer {
    padding: 20px 0; }
  .footer__block {
    flex-direction: column-reverse; }
  .footer_nav__block {
    width: 100%; }
  .footer_nav_link__block {
    width: 100%;
    flex-direction: revert;
    align-items: center;
    justify-content: space-between; }
  .footer_service__block {
    width: 100%;
    margin: 10px 0px; }
  .footer_service_link {
    margin-right: 0; }
  .footer_logo__block {
    margin: 0 auto;
    text-align: center; } }
